import styled from 'styled-components';

const LoginContainer = styled.div`
  @media all and (min-width: 480px) {
    & {
      padding: 60px 0;
    }
  
    & form {
      margin: 0 auto;
      max-width: 320px;
    }
  }
`;
export default LoginContainer;



